/**
 * picker
 *  
 * @export
 * @class data
 */
export default {
  gender() {
    return [[{
      val: 1,
      text: ''
    }, {
      val: 2,
      text: ''
    }]];
  },
  getYear(year) {
    let data = [];

    for (let i = 0; i < 100; i++) {
      if (year - i >= 1790) {
        data.unshift({
          val: year - i,
          text: (year - i) + ''
        });
      } else {
        break;
      }
    }

    for (let i = 1; i < 100; i++) {
      data.push({
        val: year + i,
        text: (year + i) + ''
      });
    }

    return data;
  },
  getMonth() {
    let data = [];
    for (let i = 1; i < 13; i++) {
      data.push({
        val: i,
        text: (i < 10 ? '0' + i : i) + ''
      });
    }
    return data;
  },
  getDay(year, month) {
    let data = [];
    for (let i = 1; i < this.day_arr(year, month) + 1; i++) {
      data.push({
        val: i,
        text: (i < 10 ? '0' + i : i) + ''
      });
    }
    return data;
  },
  // 410040010
  is_leap(year) {
    return (year % 100 === 0 ? (year % 400 === 0 ? 1 : 0) : (year % 4 === 0 ? 1 : 0));
  },
  // 
  day_arr(year, month) {
    let day_str = [];
    day_str.push(31, 28 + this.is_leap(year), 31, 30, 31, 30, 31, 31, 30, 31, 30, 31);
    return day_str[month - 1];
  },
  date(defaultDate) {
    let date = defaultDate.split('-');
    return [this.getYear(Number(date[0])), this.getMonth(), this.getDay(Number(date[0]), Number(date[1]))];
  }
}


